* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
list-style: none;
margin: 0;
padding: 0;
}

.list-item__style{
  display: flex;
  align-items: center;

}
.image__style{
  width: 60px;
  height: 50px;
}

.card__name{
  padding: 15px;
}
.name-country{
  padding: 15px;
  font-size: 24px;
  font-weight: 600;
}

.card__main{
  display: flex;
  align-items: center;
}

.card__header{
  padding: 15px;
  font-size: 32px;
  font-weight: 900;}

.flag__card{
  width: 100px;
  height: 90px;
}

.card__info{
  font-size: 24px;
  font-weight: 700;
}

.info__value{
  padding: 15px;
  font-weight: 500;
}